.monthly-calendar {
  width: 100vw;
  height: 100vh;
  padding: 10px;
  box-sizing: border-box;
  background-color: transparent;
  overflow-y: auto;
}

.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.calendar-title {
  font-size: 1.8rem;
  font-weight: bold;
  color: var(--room-card-text-color);
  margin: 0;
}

/*       */
.lock-toggle-button {
  display: flex;
  align-items: center;
  gap: 4px; /*     */
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  color: var(--room-card-text-color);
}

.lock-icon {
  font-size: 1rem;
  margin-left: 10px;
  color: rgb(0, 51, 255);
}

.lock-text {
  font-size: 1rem;
  margin-left: 5px;
}

.return-button {
  padding: 8px 16px;
  font-size: 1rem;
  border: none;
  border-radius: 4px;
  background-color: #6287ace1;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.return-button:hover {
  background-color: #007cb576;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1px;
  /*   ,    */
  align-items: start;
}

.calendar-cell {
  border: 1px solid var(--color-border-default);
  border-radius: 12px;
  background: var(--room-card-background-color);
  color: var(--room-card-text-color);
  min-height: 330px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding: 10px;
  box-sizing: border-box;
  user-select: none;
}

.calendar-cell.selected {
  border-color: var(--color-primary-soft-green);
  background-color: #e0ffe0;
}

.cell-header {
  background-color: #c9c9c9;
  padding: 10px;
  text-align: center;
  font-weight: bold;
  font-size: 1.2rem;
  border-radius: 8px;
  margin-bottom: 15px;
}

/* (, )     */
.cell-header.weekend {
  color: red;
}

.cell-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0px;
  overflow-y: auto;
  line-height: 30px;
}

.room-type {
  font-size: 1.2rem;
  height: 30px;
  background-color: transparent;
  border: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0px 16px;
  border-bottom: 1px dashed #ddd;
  cursor: pointer;
  margin: 5px;
}

/*      */
.room-type:hover {
  background-color: #dfdede;
  border-radius: 5px;
}

/*      */
.room-type.selected-room-type {
  background-color: #e0ffe0; /*    */
  border: 1px solid var(--color-primary-soft-green);
}

.weekday-header {
  display: flex;
  background-color: #f8f8f8;
  border-bottom: 2px solid #ddd;
}

.weekday-cell {
  flex: 1;
  text-align: center;
  padding: 8px 0;
  font-weight: bold;
}

/*     7  */
.calendar-grid.locked {
  grid-template-columns: repeat(7, 1fr);
}

/* ()       */
.calendar-cell.outside {
  opacity: 0.5;
}

.calendar-title {
  display: flex;
}

.weekday-header {
  display: flex;
  font-size: 1.3rem;
  background-color: #f8f8f8;
  border-bottom: 2px solid #ddd;
}

.weekday-cell.weekend {
  color: red;
}

/*   (1800px ) */
.room-type {
  font-size: 1.2rem;
}
.cell-header {
  background-color: #c9c9c9;
  padding: 10px;
  text-align: center;
  font-weight: bold;
  font-size: 1.2rem;
  border-radius: 8px;
  margin-bottom: 15px;
}

/*   1800px  */
@media (max-width: 2800px) {
  .room-type {
    font-size: 1.15rem;
  }
  .cell-header {
    font-size: 1.15rem;
  }
}

