.calendar-cell.today {
  border: 3px solid blue;
  opacity: 1; /*    */
}

/* MonthlyCalendar.css     */
.daily-nav-button {
  padding: 8px 4px;
  margin-left: 2px; /*  10px  */
  background-color: #cbcccde5;
  color: #947dfa;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.daily-nav-button:hover {
  background-color: #cfe0f2a0;
}

.confirm-popup {
  margin-left: 54px;
  background: #fff;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 4px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  z-index: 1000;
}
.confirm-popup p {
  margin: 0;
}
.confirm-popup button {
  padding: 4px 8px;
  font-size: 0.9rem;
  cursor: pointer;
}

.cell-header {
  display: flex;
  width: auto;
  margin: auto;
  gap: 5px;
  justify-content: space-evenly;
  align-items: center;
}


/*   (MonthlyCalendar.css   CSS  ) */
.room-type {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.2rem 0.5rem;
  /*   margin, border   */
}

.room-type > span:first-child {
  /*  :    */
  font-size: 1rem;
}

.room-type > .leftover-rooms {
  /*  :   */
  font-size: 0.8rem;
  color: red;
  text-align: right;
  width: 60%;
}

/*  : FHD  ( 1920px) */
@media (max-width: 1920px) {
  .room-type > span:first-child {
    font-size: 0.95rem;
  }
  .room-type > .leftover-rooms {
    font-size: 0.75rem;
    width: 55%;
  }
}

/*  : QHD  (1921px ~ 2560px) */
@media (min-width: 1921px) and (max-width: 2560px) {
  .room-type > span:first-child {
    font-size: 1.05rem;
  }
  .room-type > .leftover-rooms {
    font-size: 0.85rem;
    width: 60%;
  }
}

/*  : 27   4K  (2561px ) */
@media (min-width: 2561px) {
  .room-type > span:first-child {
    font-size: 1.2rem;
  }
  .room-type > .leftover-rooms {
    font-size: 1rem;
    width: 65%;
  }
}
