/*     */

/*    */
.header {
  box-sizing: border-box;
  position: fixed; /*    */
  top: 0;
  left: 280px; /*     */
  width: calc(100% - 280px); /*      */
  height: 140px; /*   */
  z-index: 10; /*     */
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  gap: 10px;
  padding: 20px;
  background-color: transparent;
  border-bottom: 1px solid #a7a5a53d;
}

/*  :   */
.header-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.date-navigation {
  display: flex;
  align-items: center;
}

.selected-date {
  font-size: 2rem;
  padding: 0 10px;
  margin-top: 10px;
  font-weight: 700;
  color: #050505;
}

.weekday {
  font-size: 1.2rem;
  padding-left: 5px;
  color: #555555;
}

.weekend {
  color: #ff00008e;
}

/*    */
.arrow-button {
  background: none;
  padding: 0 10px;
  margin-top: 10px;
  border: none;
  cursor: pointer;
  font-size: 2rem;
  color: #3f3f3f78;
  transition: color 0.3s ease, transform 0.3s ease;
}

.arrow-button:hover {
  color: #194fcdaa;
  transform: scale(1.1);
}

/*   :   */
.header-bottom {
  display: grid;
  /*  3 (:  , : OTA , : Quick-create) */
  grid-template-columns: 1fr 2fr 1fr;
  align-items: center;
}

/*  :     */
.header-left {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-left: 20px;
}

.additional-buttons {
  display: flex;
  gap: 10px;
}

.sort-button,
.memo-button {
  padding: 6px 12px;
  font-size: 1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #f0f0f0;
  color: #333333;
  display: flex;
  align-items: center;
  gap: 5px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.sort-button:hover,
.memo-button:hover {
  background-color: #e0e0e0;
  transform: scale(1.05);
}

.sort-button.active,
.memo-button.active {
  background-color: #d0d0d0;
}

/*  : OTA   */
.header-ota-status {
  display: flex;
  justify-content: center;
}

.ota-status-item {
  margin: 0 10px;
  font-size: 16px;
  display: flex;
  align-items: center;
}

.status-lamp {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-left: 8px;
}

.status-lamp.green {
  background-color: rgb(131, 239, 131);
}

.status-lamp.gray {
  background-color: gray;
}

/*  : Quick-create   */
.header-right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 20px;
}

.quick-create-buttons {
  display: flex;
  align-items: center;
  gap: 10px;
}

/*  quick-button     */
.quick-button {
  padding: 6px 12px;
  font-size: 1.1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #6287ace1;
  color: #ffffff;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.monthly-view-button {
  padding: 6px 12px;
  font-size: 1.1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #9662ace1;
  color: #ffffff;
  transition: background-color 0.3s ease, color 0.3s ease;
  white-space: nowrap;
}

.quick-button:hover {
  background-color: #007cb576;
}

.quick-button-green {
  background-color: #4caf4fe9;
  color: #ffffff;
}

.quick-button-green:hover {
  background-color: #45a04979;
}

/*    */
.header-fade {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 10px;
  background: linear-gradient(
    to bottom,
    rgba(244, 244, 244, 0),
    rgba(244, 244, 244, 1)
  );
}

/*  :   2200px   */
@media (max-width: 2200px) {
  /* OTA   */
  .header-ota-status {
    display: none;
  }
  /*    2(:  , : Quick-create )  */
  .header-bottom {
    grid-template-columns: 1fr 1fr;
  }
  /* Quick-create       quick    */
  .quick-create-buttons .quick-button:not(.monthly-view-button) {
    display: none !important;
  }
  /* quick-create-buttons gap     */
  .quick-create-buttons {
    gap: 0;
    justify-content: flex-end;
  }
}
