/* ==========================================================================
   [Global Variables for Card Layout]
========================================================================== */
:root {
  --card-width: 280px;
  --card-height: 360px;
  --card-header-height: 40px;
  --card-footer-height: 30px;
  --card-body-height: calc(
    var(--card-height) - var(--card-header-height) - var(--card-footer-height)
  );

  --room-card-background-color: #ffffff;
  --room-card-border: #fcfcfc;
  --room-card-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  --min-gap: 16px;
}

/* ==========================================================================
   [Room Grid Container]
========================================================================== */
.layout-grid {
  margin-left: 30px;
  display: grid;
  grid-template-columns: repeat(
    auto-fit,
    minmax(280px, 320px 1fr)
  ); /* 280px     */
  gap: 10px;
  width: 100%;
  box-sizing: border-box;
  border: none;
}

.floor-section {
  gap: 20px;
}
/* ==========================================================================
   [Room Cell Container]
========================================================================== */
.room-cell {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  border: 1px solid #fcfcfc;
  border-radius: 8px;
  overflow: hidden;
  min-width: 280px; /*    */
  max-width: 320px; /*    */
  min-height: var(--card-height);
}

.room-cell-header {
  flex: 0 0 40px;
  background-color: #e9ecef;
  border-bottom: 1px solid #dddddd;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  font-weight: bold;
  padding: 0 8px;
}

.room-cell-body {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  overflow: hidden;
}

/* ==========================================================================
   [Reservation Card - New Structure]
    (+) ()  .
========================================================================== */
.reservation-card {
  width: var(--card-width);
  height: var(--card-height);
  display: flex;
  flex-direction: column;
  background-color: var(--room-card-background-color);
  border: 2px solid var(--room-card-border);
  border-radius: 8px;
  box-shadow: var(--room-card-shadow);
  overflow: hidden;
  margin: 5px;
}

/*  :    */
.reservation-card-top {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.reservation-card-head {
  flex: 0 0 var(--card-header-height);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 4px 8px;
  background-color: #f8f9fa;
  border-bottom: 1px solid #dddddd;
  font-size: 0.9rem;
}

.reservation-card-body {
  flex: 1;
  padding: 8px;
  overflow-y: auto;
  font-size: 0.9rem;
  line-height: 1.3;
}

/*  :     */
.reservation-card-footer {
  flex: 0 0 var(--card-footer-height);
  background-color: #f3f3f3;
  border-top: 1px solid #dddddd;
  text-align: center;
  font-size: 0.85rem;
  padding: 4px;
}

/* ==========================================================================
   [Action Buttons on Reservation Card Header]
========================================================================== */
.reservation-card-header .action-buttons {
  display: flex;
  gap: 4px;
}

.reservation-card-header .action-button {
  background-color: #27ae60;
  color: #623e3e;
  border: none;
  border-radius: 4px;
  padding: 2px 2px;
  font-size: 0.75rem;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.reservation-card-header .action-button:hover {
  transform: scale(1.05);
}

/* (  h3   flex   ) */
.no-break {
  display: flex;
  align-items: center;
  justify-content: space-between;
  white-space: nowrap;
  margin-bottom: 5px;
  z-index: 1000;
}

.button-group-wrapper {
  display: flex;
  /* gap: 6px; */
}

/* ==========================================================================
   [Responsive & Minimum Size]
========================================================================== */
.reservation-card,
.room-cell {
  min-width: var(--card-width);
  min-height: var(--card-height);
  box-sizing: border-box;
}

@media (max-width: 600px) {
  .room-grid-container {
    grid-template-columns: repeat(auto-fill, minmax(var(--card-width), 1fr));
  }
}
/* ==========================================================================
   [Room Card Edit Mode]
========================================================================== */
.room-card.edit-mode {
  margin-left: 100px;
  width: 500px; /*   */
  height: 470px;
  background: #ffffff; /*    */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /*   */
  border: 2px solid #1a237e; /*   ( ) */
  border-radius: 8px;
  padding: 30px;
  position: relative;
  z-index: 1000;
  transform: scale(1.2); /*  */
  display: flex;
  flex-direction: column;
  overflow: hidden;
  font-weight: 700;
}

.edit-card-content {
  display: flex;
  flex-direction: column;
  gap: 10px;
  height: 100%;
  background: #ffffff;
  border-radius: 0; /*    */
  box-sizing: border-box;
}

.edit-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
  padding: 5px 0;
  border-bottom: 1px solid #ddd;
}

.edit-card-header h2 {
  font-size: 1.2rem;
  font-weight: 700;
  color: #1a237e;
  margin: 0;
}

.edit-card-header .close-button {
  background: none;
  border: none;
  font-size: 1rem;
  color: #666;
  cursor: pointer;
  transition: color 0.3s ease;
}

.edit-card-header .close-button:hover {
  color: #d32f2f;
}

.edit-card-row {
  display: flex;
  gap: 10px;
  margin-bottom: 5px;
}

.edit-card-row label {
  flex: 1;
  display: flex;
  flex-direction: column;
  font-weight: 500;
  color: #333;
  font-size: 0.9rem;
}

.edit-card-row input,
.edit-card-row select {
  margin-top: 5px;
  padding: 6px;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  font-size: 0.9rem;
  background-color: #fff;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
  box-sizing: border-box;
  width: 100%;
}

.edit-card-row input:focus,
.edit-card-row select:focus {
  border-color: #1a237e;
  box-shadow: 0 0 5px rgba(26, 35, 126, 0.2);
  outline: none;
}

.edit-card-actions {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  padding: 100px;
  margin-top: 10px;
  padding-top: 5px;
  border-top: 1px solid #ddd;
}

.edit-card-actions button {
  font-size: 0.9rem;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  flex: 2;
  max-width: 100px;
  font-weight: 600;
}

.edit-card-actions .save-button {
  background-color: #1a237e;
  color: #fff;
}

.edit-card-actions .save-button:hover {
  background-color: #142a6b;
  transform: scale(1.05);
}

.edit-card-actions .cancel-button {
  background-color: #e57373;
  color: #fff;
}

.edit-card-actions .cancel-button:hover {
  background-color: #d32f2f;
  transform: scale(1.05);
}
/* ==========================================================================
   [Flip Effect & Other Card Styles]
   (       )
========================================================================== */

.flip-container {
  perspective: 1000px;
  height: 100%;
}
.room-card-inner {
  min-width: 280px;
  max-height: 360px;
  box-sizing: border-box;
  height: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}
.flip-container.flipped .room-card-inner {
  transform: rotateY(180deg);
}
.room-card-back {
  transform: rotateY(180deg);
  padding: 0;
  box-sizing: border-box;
  backface-visibility: hidden;
}
.room-card-back::before {
  display: none;
  backface-visibility: hidden;
}
.room-card.flipped:hover,
.room-card.flipped:hover::before {
  transform: none !important;
  background-color: var(--room-card-background-color) !important;
  z-index: auto !important;
}
.flip-container.auto-flip .room-card-inner {
  animation: autoFlip 1s ease-in-out;
}
@keyframes autoFlip {
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(-30deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}
.flipped .room-card-inner {
  transform: rotateY(180deg);
}

/* ==========================================================================
   [Miscellaneous: Action Buttons, Labels, Memo, etc.]
========================================================================== */
.button-group {
  display: inline-flex;
  gap: 5px;
  align-items: center;
  box-sizing: border-box;
  width: 120px; /*  :  3   ( 40px * 3) */
  position: relative;
  height: 28px;
  overflow: hidden;
}

.button-group-wrapper {
  display: flex;
  gap: 6px;
  margin: 0px 5px;
}

.action-button {
  font-size: 0.8rem;
  border: none;
  cursor: pointer;
  padding: 6px 10px;
  height: 28px;
  line-height: 1;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: width 0.3s ease, background-color 0.3s ease; /*    */
  border-radius: 4px;
  font-weight: 600;
  position: relative;
  overflow: hidden;
  width: 40px; /*   */
  z-index: 2;
}

.action-button span {
  display: inline-block;
  transition: transform 0.3s ease;
}

/*    */
.action-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0; /*  :   */
  height: 100%;
  background: rgba(255, 255, 255, 0.3); /*    */
  transition: width 0.3s ease; /*    */
  z-index: -1; /*   */
}

.delete-button {
  background-color: var(--color-accent-coral);
  color: #f00d0d;
}

.delete-button:hover {
  background-color: #e57373;
  width: 120px; /* 3  */
}

.delete-button:hover::before {
  width: 100%; /*    */
}

.delete-button:hover span {
  transform: translateY(-2px);
}

.confirm-button {
  background-color: var(--color-primary-deep-blue);
  color: #1018f9;
}

.confirm-button:hover {
  background-color: #3f51b5;
  width: 120px; /* 3  */
}

.confirm-button:hover::before {
  width: 100%; /*    */
}

.confirm-button:hover span {
  transform: translateY(-2px);
}

.edit-button {
  background-color: var(--color-primary-soft-green);
  color: #383938;
}

.edit-button:hover {
  background-color: #81c784;
  width: 120px; /* 3  */
}

.edit-button:hover::before {
  width: 100%; /*    */
}

.edit-button:hover span {
  transform: translateY(-2px);
}

/*      */
.button-group:hover .action-button:not(:hover) {
  opacity: 0;
  width: 0;
  padding: 0;
  transition: all 0.3s ease;
}

.small-button {
  opacity: 0.95;
  font-size: 0.95rem;
  padding: 6px 8px;
}

.small-button:hover {
  opacity: 1;
}

/* :      ( ) */
.action-button::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: rgba(255, 255, 255, 0.3); /*    */
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: width 0.3s ease, height 0.3s ease;
  pointer-events: none; /*    */
}

.action-button:hover::after {
  width: 200%;
  height: 200%;
}

/*  .no-break  */
