/* ==========================================================================
   [Memo Section inside Card Back]
========================================================================== */
.reservation-list {
  position: relative;
}
.memo-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #919191;
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px 5px 0 0;
  cursor: pointer;
  z-index: 1;
}
.memo-body {
  flex: 1;
  padding: 5px 10px;
  overflow-y: auto;
  background-color: transparent;
  color: #000;
}
.memo-text-display {
  white-space: pre-wrap;
  word-wrap: break-word;
  font-size: 1rem;
  line-height: 1.4;
  color: rgb(58, 77, 91);
}
.memo-textarea {
  width: 100%;
  height: 100%;
  min-height: 250px;
  background: transparent;
  border: none;
  outline: none;
  resize: none;
  padding: 5px;
  box-sizing: border-box;
  font-size: 1.2rem;
}
.memo-room-number {
  color: rgba(0, 0, 0, 0.5);
  font-size: 1.2rem;
}
.memo-edit-button {
  background: none;
  border: none;
  cursor: pointer;
  color: #fff;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: color 0.3s ease, transform 0.2s ease;
}
.memo-edit-button:hover {
  color: #f1c40f;
  transform: scale(1.2);
}
