/*   */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 130vh;
  width: 550px;
  background: linear-gradient(135deg, #ece9e6, #d7d6d6);
  padding: 20px;
}

/*   */
.login-form {
  background: #ffffff;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 500px;
  text-align: center;
  position: relative;
  border: 1px solid rgb(207, 207, 207);
}

/*  */
.login-title {
  font-size: 28px;
  font-weight: 700;
  color: #1a237e;
  margin-bottom: 20px;
}

/*   */
.login-error-message {
  color: #d32f2f;
  font-size: 14px;
  margin-bottom: 10px;
  text-align: left;
  padding: 8px;
  border-radius: 4px;
  background-color: rgba(255, 0, 0, 0.1);
}

/*   */
.login-input-group {
  margin-bottom: 20px;
  position: relative;
}

/*   */
.login-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  border: 2px solid #809dfa;
  border-radius: 6px;
  background: #f5f5f5;
  height: 54px;
  padding: 0 10px;
  transition: border-color 0.3s, box-shadow 0.3s;
}

/*   */
.login-input-field {
  width: 100%;
  padding: 12px 16px;
  font-size: 16px;
  border: none;
  background: transparent;
  outline: none;
  height: 48px;
}

/*   */
.login-input-field:focus {
  background: #fff;
  border-color: #1a237e;
  box-shadow: 0 0 8px rgba(26, 35, 126, 0.2);
}

/* placeholder   */
.login-input-field::placeholder {
  color: transparent;
  opacity: 0;
}

/*     (input   ) */
.login-input-field:placeholder-shown:not(:focus) + .login-input-label {
  top: 12px;
  font-size: 14px;
  color: #8e8e8e;
  opacity: 1;
}

/*       */
.login-input-field:not(:placeholder-shown) + .login-input-label,
.login-input-field:focus + .login-input-label {
  top: -8px;
  font-size: 12px;
  color: #262626;
  background: #ffffff;
  padding: 0 4px;
  opacity: 1;
}

/*    */
.login-input-label {
  position: absolute;
  left: 16px;
  transition: all 0.3s ease;
  pointer-events: none;
  font-weight: 500;
  z-index: 1;
}

.login-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  border: 2px solid #809dfa;
  border-radius: 6px;
  background: #f5f5f5;
  transition: border-color 0.3s, box-shadow 0.3s;
  height: 54px;
  outline: none;
  box-sizing: border-box;
}

.login-input-wrapper:hover {
  border-color: #999;
  box-sizing: border-box;
}

.login-input-field {
  width: 80%;
  padding: 12px 16px;
  font-size: 24px;
  border: none;
  border-radius: 6px;
  outline: none;
  background: transparent;
  transition: background-color 0.3s ease;
  box-sizing: border-box; /*     */
  height: 48px; /*    */
  line-height: 29px; /*   ,    */
  margin: 0; /*   */
  font-family: inherit; /*    */
}

/* type="password" type="text"    */
.login-input-field[type='text'],
.login-input-field[type='password'] {
  width: 80%;
  height: 18px; /*   */
  margin-bottom: 0px;
  padding: 12px 16px; /*   */
  line-height: 20px; /*     */
  font-size: 16px; /*    */
  border: none;
  box-sizing: border-box;
  background-color: transparent;
}

/*        */
.login-input-field::-ms-clear,
.login-input-field::-ms-reveal {
  display: none;
}

.login-input-field:focus {
  background-color: #fff;
  border-color: #1a237e;
  box-shadow: 0 0 8px rgba(26, 35, 126, 0.2);
}

/*    */
.login-password-toggle-icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  color: #555;
  cursor: pointer;
  font-size: 18px;
  transition: color 0.3s ease;
  height: 48px; /*     */
  line-height: 48px; /*     */
}

.login-password-toggle-icon:hover {
  color: #333;
}

/*   */
.login-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px 0;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
}

.login-checkbox-label {
  font-size: 14px;
  color: #555;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.login-checkbox-text {
  margin-left: 5px;
}

.login-ip-security {
  font-size: 14px;
  color: #1a237e;
  font-weight: 600;
  transition: color 0.3s ease;
}

.login-ip-security:hover {
  color: #142a6b;
}

/*   */
.login-login-button {
  width: 100%;
  padding: 12px;
  background-color: #c9c9c9 !important; /*    */
  color: #5a5858 !important;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.9s ease, transform 0.9s ease, color 0.9s ease;
  margin-bottom: 20px;
}

.login-login-button:hover:not(:disabled) {
  background-color: #bbbbbb !important; /*       */
  transform: translateY(-2px);
  color: #ffffff !important;
}

.login-login-button:disabled {
  background-color: #f0f0f0; /*      */
  cursor: not-allowed;
  transform: translate();
  color: #999;
}

/*   (Passkey  QR) */
.login-auth-options-bottom {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.login-passkey-button,
.login-qr-button {
  width: 48%;
  padding: 12px;
  background: #e0e0e0;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  color: #555;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
}

.login-passkey-button:hover,
.login-qr-button:hover {
  background: #d5d5d5;
  transform: translateY(-2px);
}

.login-qr-button svg {
  width: 24px;
  height: 24px;
  fill: #1a237e;
}

/*   */
.login-forgot-or-signup {
  font-size: 14px;
  color: #555;
}

.login-forgot-link,
.login-signup-link {
  color: #1a237e;
  text-decoration: none;
  cursor: pointer;
  font-weight: 600;
  transition: color 0.3s ease, opacity 0.3s ease;
}

.login-forgot-link:hover,
.login-signup-link:hover {
  color: #142a6b;
  text-decoration: underline;
}

.login-signup-link {
  opacity: 0.6; /*   */
}

.login-signup-link:hover {
  opacity: 1; /*    */
}

/* QR   */
.login-qr-modal {
  text-align: center;
}

.login-qr-modal h3 {
  font-size: 20px;
  color: #333;
  margin-bottom: 20px;
}

.login-qr-code-image {
  margin-bottom: 20px;
}

.login-qr-modal p {
  font-size: 14px;
  color: #555;
  margin-bottom: 15px;
}

.login-qr-instruction {
  font-size: 12px;
  color: #777;
  line-height: 1.5;
}

.login-qr-valid-time {
  font-size: 14px;
  color: #555;
  margin-bottom: 10px;
  font-weight: 500;
}

.login-close-modal {
  padding: 10px 20px;
  background: #1a237e;
  color: #fff;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: background 0.3s, transform 0.2s ease;
}

.login-close-modal:hover {
  background: #142a6b;
  transform: translateY(-2px);
}
/*      */
.login-error-message {
  color: #d32f2f;
  font-size: 14px;
  margin-bottom: 10px;
  text-align: left;
  padding: 8px;
  border-radius: 4px;
  background-color: rgba(255, 0, 0, 0.1); /*    */
  transition: all 0.3s ease; /*   */
}

/*    */
.login-error-attempt-1 {
  border-left: 4px solid #ff9800; /*   */
}

.login-error-attempt-2 {
  border-left: 4px solid #f44336; /*    */
}

.login-error-attempt-3 {
  border-left: 4px solid #d32f2f; /*    */
  animation: shake 0.5s ease-in-out; /*   */
}

.login-error-attempt-4 {
  border-left: 4px solid #b71c1c; /*     */
  animation: shake 0.5s ease-in-out;
  background-color: rgba(255, 0, 0, 0.15); /*    */
}

.login-error-attempt-5 {
  border-left: 4px solid #880e4f; /*   ( ) */
  animation: shake 0.5s ease-in-out;
  background-color: rgba(255, 0, 0, 0.2); /*    */
  font-weight: bold; /*   */
}

/*   */
@keyframes shake {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  50% {
    transform: translateX(5px);
  }
  75% {
    transform: translateX(-5px);
  }
  100% {
    transform: translateX(0);
  }
}

/*       ( ) */
.login-error-reset-required {
  color: #880e4f;
  font-size: 16px;
  font-weight: bold;
  background-color: rgba(136, 14, 79, 0.1);
  border-left: 4px solid #880e4f;
  animation: pulse 1s infinite; /*   */
}

/*   */
@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
